import React, { Component } from 'react'
import {Carousel} from 'react-bootstrap'
export class InfoCarousel extends Component {
    render() {
        return (
            <div class="row container">
                <Carousel>
                    
                    <Carousel.Item style={{width: '25%'}}>
                        <img className="d-block w-100" src="https://www.pngkey.com/png/detail/178-1787508_github-icon-download-at-icons8-white-github-icon.png" alt="First slide"
                        style={{width: '55%'}}
                        />
                        <div className="col-md-4">
                            <Carousel.Caption style={{color: 'grey'}}>
                            <h3>Firsta slide label</h3>
                            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                            </Carousel.Caption>
                            </div>
                        </Carousel.Item>
                    <Carousel.Item>
                        <img
                        className="d-block w-100"
                        src="https://cdn4.iconfinder.com/data/icons/logos-brands-5/24/freecodecamp-512.png"
                        alt="Second slide"
                        />
                        <Carousel.Caption>
                            <h3>Second slide label</h3>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                        className="d-block w-100"
                        src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/e9/Linkedin_icon.svg/1200px-Linkedin_icon.svg.png"
                        alt="Third slide"
                        />

                        <Carousel.Caption>
                        <h3>Third slide label</h3>
                        <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </div>
        )
    }
}

export default InfoCarousel
